.tab-list {
    width: 7%;
    height: 100%;   
    position: fixed;
    margin: 0;
    padding: 0;
    background-color: #ccc;
}

.tab-list-item {
    display: block;
    list-style: none;
    color: white;
}

.tab-list-item:hover {
    background-color: #aaa;
}

.tab-list-active {
    background-color: #999;
}

.tab-content {
    margin-left: 7%;
}

  
  